.header{
  background: #FFFFFF;
  width: 100%;
  display: flex;
  flex-direction:column;
  border-bottom: 0.1vw solid black;
  margin-top: 1vw;
}
.countTovar{
  width: 1.4vw;
  border-radius: 50%;
  background: #82C9DC;
  font-size: 1.2vw;
text-align: center;
position: absolute;
margin-left: -2.5vw;
margin-top: -1.5vw;
font-family: 'Montserrat';
font-style: normal;
color: #FFFFFF;
}
.OneFotoGroupe{
height: 2vw;
padding-right: 2vw;
}
.logo{
font-family: 'Philosopher';
font-style: normal;
font-weight: 600;
color: rgba(68, 68, 68, 1);
text-decoration: none;
font-size: 3vw;
}

.groupe11{
  display: flex;
  flex-direction: row;
  align-items: center;
}
.phone{
  font-family: 'Montserrat';
font-style: normal;
font-weight: 300;
font-size: 1.2vw;
line-height:1.5;
text-transform: capitalize;
color: #444444;
}

.up{
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
}
@media only screen and (min-width: 769px) and (max-width: 1024px){
  .header{
    padding-top: 1vw;
  }
.logo{
  font-weight:600;
  font-size: 4vw;
}
.OneFotoGroupe{
height: 2.5vw;
padding-right: 2vw;
}
.phone{
  font-size: 1.5vw;
}
.countTovar{
width: 2.3vw;
font-size: 1.8vw;
margin-left: -3vw;
margin-top: -2.5vw;
}
  }
@media only screen and (min-width: 481px) and (max-width: 768px){
   .header{
    padding-top: 1vw;
  }
.logo{
  font-weight:600;
  font-size: 5vw;
}
.OneFotoGroupe{
height: 3vw;
padding-right: 2vw;
}
.phone{
  font-size: 1.7vw;
}
.countTovar{
width: 2.5vw;
border-radius: 50%;
background: #82C9DC;
font-size: 2vw;
text-align: center;
position: absolute;
margin-left: -2.6vw;
margin-top: -3vw;
}
  }
@media only screen and (min-width: 320px) and (max-width: 480px){
  .header{
    padding-top: 1vw;
  }
.logo{
  font-weight:600;
  font-size: 5vw;
}
.OneFotoGroupe{
height: 3vw;
padding-right: 1vw;
}
.phone{
  font-size: 2vw;
}
.countTovar{
  width: 2.5vw;
    border-radius: 50%;
  background: #82C9DC;
  font-size: 2vw;
text-align: center;
position: absolute;
margin-left: -2vw;
margin-top: -3vw;
}
}